button {
  margin-left: 10px;
  border-radius: 20px;
  border: 0rem;
  background-color: bisque;
  -webkit-touch-callout: none; /* iOS Safari */
  -webkit-user-select: none; /* Safari */
  -khtml-user-select: none; /* Konqueror HTML */
  -moz-user-select: none; /* Old versions of Firefox */
  -ms-user-select: none; /* Internet Explorer/Edge */
  user-select: none;
  padding: 5px 10px 5px 10px;
}

button:focus {
  outline: 0;
}

input {
  border: 0rem;
  border-radius: 10px;
  padding: 5px 10px 5px 10px;
}

.heading {
  color: white;
}

.list-item-wrapper {
  display: flex;
  flex-flow: row;
  width: 95%;
  justify-content: space-between;
  background-color: rgba(60, 233, 233, 0.778);
  padding: 5px;
  border-radius: 10px;
  margin-top: 10px;
}
.item-description {
  margin-left: 10px;
  border-radius: 10px;
  padding-left: 10px;
}

.item-edit-button {
  opacity: 0.7;
  transition: all 0.2s;
}
.item-delete-button {
  opacity: 0.7;
  transition: all 0.2s;
}
.item-save-button {
  opacity: 0.7;
  transition: all 0.2s;
}

.item-edit-button button {
  background-color: rgb(211, 234, 35);
  color: white;
}
.item-delete-button button {
  background-color: rgba(255, 0, 0, 0.785);
  color: white;
}
.item-save-button button {
  background-color: rgba(34, 255, 0, 0.743);
}

.item-edit-button:hover {
  opacity: 1;
}
.item-delete-button:hover {
  opacity: 1;
}
.item-save-button:hover {
  opacity: 1;
}

.item-buttons {
  display: flex;
  flex-flow: row;
  justify-content: space-between;
  transition: all 0.2s;
}

.add-item-wrapper {
  margin-top: 20px;
}

.add-item-button {
  margin-left: 10px;
  transition: all 0.2s;
  background-color: rgba(0, 255, 72, 0.576);
  color: rgb(255, 255, 255);
}

.add-item-button:hover {
  background-color: rgb(73, 207, 162);
}

.add-item-button:focus {
  outline: 0;
}

.list-wrapper {
  width: 70%;
  /* background-color: coral; */
  background-image: linear-gradient(
    to left,
    rgba(1, 243, 90, 0.5),
    rgb(0, 183, 255)
  );
  padding: 1rem;
  margin-left: auto;
  margin-right: auto;
  margin-top: 2rem;
  border-radius: 15px;
  box-shadow: 0px 0px 20px 0px rgb(255, 255, 255, 0.4);
}
